import Vue from 'vue'

const components = {
  icon: () => import('./ui/icon'),
  tabs: () => import('./ui/tabs'),
  formInput: () => import('./form/form-input'),
  formField: () => import('./form/form-field'),
  check: () => import('./ui/check'),
  btn: () => import('./ui/btn'),
  spinner: () => import('./ui/spinner'),
  collapse: () => import('./ui/collapse'),
}

Object.entries(components).forEach(([name, component]) => Vue.component(name, component))
